export default `
<script>
{
    let ages = [32, 33, 16, 40]
    let younger = Array.from(ages, age => age-10)
    write(...younger)
}
{
    let ages = [32, 33, 16, 40]
    let younger = Array.from(ages, age => '年龄为：'+age)
    write(...younger)
}
{
    let single = Array.from('wdbBlog')
    write(single)
}
</script>
`



